.ul-design {
    background-color: salmon;
    padding: 20px;
    margin-top: -1px;
}

.ul-design a {
    text-decoration: none;
    margin-right:  100px;
    color: rgb(255, 255, 255);
    font-size: 20px;

}

.ul-design a:hover {
    background-color: rgb(154, 49, 196);
   padding: 10px 5px ;
   border-radius: 5px;


}
.body{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    column-gap: 10px;
}
